import { safeStringify, addTimestamp } from "@/client/utils";
import type { ErrorPayload } from "@/client/types";

/**
 * éŒ¯èª¤æ”¶é›†å™¨ - å…¨åŸŸéŒ¯èª¤ç›£æ§å’Œæ”¶é›†
 *
 * æŠ€è¡“å¯¦ä½œåŸºç¤ï¼š
 * Observer Pattern è§€å¯Ÿè€…æ¨¡å¼ + Event Listener Pattern
 * è·è²¬ï¼šç›£è½å…¨åŸŸéŒ¯èª¤äº‹ä»¶ï¼Œçµ±ä¸€æ”¶é›†å’Œæ ¼å¼åŒ–
 * - **JavaScript é‹è¡Œæ™‚éŒ¯èª¤**ï¼šæ•ç² `ErrorEvent` é¡å‹çš„ JS éŒ¯èª¤
 * - **è³‡æºè¼‰å…¥éŒ¯èª¤**ï¼šæ•ç²åœ–ç‰‡ã€è…³æœ¬ã€æ¨£å¼ç­‰è³‡æº 404 éŒ¯èª¤
 * - **Promise æ‹’çµ•ç›£æ§**ï¼šç›£æ§æœªè™•ç†çš„ `unhandledrejection` äº‹ä»¶
 * - **éŒ¯èª¤å»é‡**ï¼šä½¿ç”¨çµæ§‹åŒ–çš„ key ç­–ç•¥é¿å…é‡è¤‡éŒ¯èª¤
 * - **éŒ¯èª¤åˆ†é¡**ï¼šåŒºåˆ† JS éŒ¯èª¤ã€è³‡æºéŒ¯èª¤ã€Promise éŒ¯èª¤
 */

const createErrorCollector = ({
  callback,
}: {
  callback: (data: ErrorPayload) => void;
}) => {
  const { error } = console;
  console.error = (...args: unknown[]) => {
    const payload: ErrorPayload = {
      // name:console.error ç¬¬ä¸€å€‹åƒæ•¸å¦‚æœæ˜¯ Error ç‰©ä»¶,è®€å–åç¨±ï¼Œå¦å‰‡ç‚º Error
      name: args[0] instanceof Error ? args[0].name : "Error",
      // message:console.error ç¬¬ä¸€å€‹åƒæ•¸å¦‚æœæ˜¯ Error ç‰©ä»¶,è®€å–è¨Šæ¯ï¼Œå¦å‰‡ç‚ºç¬¬ä¸€å€‹åƒæ•¸
      message:
        args[0] instanceof Error ? args[0].message : safeStringify(args[0]),
      // stack:console.error ç¬¬ä¸€å€‹åƒæ•¸å¦‚æœæ˜¯ Error ç‰©ä»¶,è®€å–å †ç–Šï¼Œå¦å‰‡ç‚ºç©º
      stack: args[0] instanceof Error ? args[0].stack : "",
      cause: args[0] instanceof Error ? args[0].cause : undefined,
      timestamp: addTimestamp(),
      source: {
        tabId: "",
        url: location.pathname + location.search + location.hash, //ç”¨ä¾†æª¢è¦–logçš„é é¢è·¯å¾‘è·Ÿå…¶åƒæ•¸
        origin: location.origin, //åŸŸå
      },
    };
    callback(payload);
    error.apply(console, args);
  };
  const onError = (e: ErrorEvent | Event) => {
    const payload: ErrorPayload = {
      // name:å¦‚æœæ˜¯ErrorEvent,è®€å–åç¨±ï¼Œå¦å‰‡ç‚ºError
      name: e instanceof ErrorEvent ? e.error?.name || "Error" : "Error",
      // message:å¦‚æœæ˜¯ErrorEvent,è®€å–è¨Šæ¯ï¼Œå¦å‰‡ç‚ºåºåˆ—åŒ–å¾Œçš„éŒ¯èª¤ç‰©ä»¶
      message: e instanceof ErrorEvent ? e.error?.message : safeStringify(e),
      // stack:å¦‚æœæ˜¯ErrorEvent,è®€å–[éŒ¯èª¤ç™¼ç”Ÿä½ç½®stack]ï¼Œå¦å‰‡ç‚ºç©º
      stack: e instanceof ErrorEvent ? e.error?.stack : "",
      timestamp: addTimestamp(),
      source: {
        tabId: "",
        url: location.pathname + location.search + location.hash, //ç”¨ä¾†æª¢è¦–logçš„é é¢è·¯å¾‘è·Ÿå…¶åƒæ•¸
        origin: location.origin, //åŸŸå
      },
    };
    callback(payload);
  };

  const onRejection = (errorEvent: PromiseRejectionEvent) => {
    const raw = `Promise UnhandledRejection: ${safeStringify(errorEvent.reason)}`;
    // EX: PR|Promise UnhandledRejection: undefined
    // const key = `PR|${raw}`;
    const payload: ErrorPayload = {
      name:
        errorEvent.reason && errorEvent.reason instanceof Error
          ? errorEvent.reason.name
          : "Error",
      message:
        errorEvent.reason && errorEvent.reason instanceof Error
          ? errorEvent.reason.message
          : raw,
      stack:
        errorEvent.reason && errorEvent.reason instanceof Error
          ? errorEvent.reason.stack
          : "",
      timestamp: addTimestamp(),
      source: {
        tabId: "",
        url: location.pathname + location.search + location.hash, //ç”¨ä¾†æª¢è¦–logçš„é é¢è·¯å¾‘è·Ÿå…¶åƒæ•¸
        origin: location.origin, //åŸŸå
      },
    };
    callback(payload);
  };

  const start = () => {
    window.addEventListener("error", onError);
    window.addEventListener("unhandledrejection", onRejection);
  };
  const stop = () => {
    window.removeEventListener("error", onError);
    window.removeEventListener("unhandledrejection", onRejection);
    console.error = error;
  };
  return {
    start,
    stop,
  };
};

export { createErrorCollector };
