/**
 * æ§åˆ¶å°æ””æˆªå™¨ - æ””æˆªå’Œç›£æ§åŸç”Ÿ console æ–¹æ³•
 *
 * æŠ€è¡“å¯¦ä½œåŸºç¤ï¼š
 * Interceptor Pattern æ””æˆªå™¨æ¨¡å¼
 * è·è²¬ï¼šæ•´è£åŸç”Ÿ APIï¼Œä¸ç ´å£åŸæœ‰åŠŸèƒ½ï¼Œå¢åŠ ç›£æ§èƒ½åŠ›
 * - **æ–¹æ³•æ””æˆª**ï¼šæ””æˆª `console.log`ã€`console.error`ã€`console.warn`ã€`console.info`
 * - **æ•¸æ“šåºåˆ—åŒ–**ï¼šå®‰å…¨åºåˆ—åŒ–å„ç¨®æ•¸æ“šé¡å‹ï¼ˆé€é `safeStringify`ï¼‰
 * - **æ™‚é–“æˆ³å¢åŠ **ï¼šç‚ºæ‰€æœ‰è¨Šæ¯å¢åŠ æ™‚é–“æˆ³ï¼ˆé€é `addTimestamp`ï¼‰
 * - **åŸå§‹åŠŸèƒ½ä¿æŒ**ï¼šä½¿ç”¨ `apply` èª¿ç”¨åŸå§‹ console æ–¹æ³•
 * - **å¯é‚„åŸæ€§**ï¼šæä¾› `restoreLog` æ–¹æ³•æ¢å¾©åŸå§‹ç‹€æ…‹
 *
 */

const createConsoleInterceptor = ({
  callback,
}: {
  callback: (data: ConsolePayload) => void;
}) => {
  const { log, warn, info } = console;
  const start = () => {
    console.log = (...args: unknown[]) => {
      const payload: ConsolePayload = {
        level: "log",
        args,
        message: args.map(safeStringify).join(" "),
        timestamp: addTimestamp(),
        source: {
          tabId: "",
          url: location.pathname + location.search + location.hash, //ç”¨ä¾†æª¢è¦–logçš„é é¢è·¯å¾‘è·Ÿå…¶åƒæ•¸
          origin: location.origin, //åŸŸå
        },
      };
      callback(payload);
      // åŒç†apply(console, args)ä½†æ˜¯æ›´ç°¡æ½”æ›´ç¾ä»£
      // console[payload.level](...args);
      log.apply(console, args);
    };
    console.warn = (...args: unknown[]) => {
      const payload: ConsolePayload = {
        level: "warn",
        args,
        message: args.map(safeStringify).join(" "),
        timestamp: addTimestamp(),
        source: {
          tabId: "",
          url: location.pathname + location.search + location.hash, //ç”¨ä¾†æª¢è¦–logçš„é é¢è·¯å¾‘è·Ÿå…¶åƒæ•¸
          origin: location.origin, //åŸŸå
        },
      };
      callback(payload);

      // console[payload.level](...args);
      warn.apply(console, args);
    };

    console.info = (...args: unknown[]) => {
      const payload: ConsolePayload = {
        level: "info",
        args,
        message: args.map(safeStringify).join(" "),
        timestamp: addTimestamp(),
        source: {
          tabId: "",
          url: location.pathname + location.search + location.hash, //ç”¨ä¾†æª¢è¦–logçš„é é¢è·¯å¾‘è·Ÿå…¶åƒæ•¸
          origin: location.origin, //åŸŸå
        },
      };
      // console[payload.level](...args);
      callback(payload);
      info.apply(console, args);
    };
  };

  const stop = () => {
    console.log = log;
    console.info = info;
    console.warn = warn;
  };

  return {
    stop,
    start,
  };
};

export { createConsoleInterceptor };
