import type { ConsolePayload, ErrorPayload } from "@/client/types";
import { LEVEL_COLOR_MAP } from "../constants";
/** è¦–è¦ºå±¤å°æ‡‰ï¼šlevel -> color class */

/**
 * å®‰å…¨åœ°è½‰ç¾© HTML å­—ç¬¦
 * @param text - è¦è½‰ç¾©çš„æ–‡æœ¬
 * @returns è½‰ç¾©å¾Œçš„ HTML å®‰å…¨å­—ç¬¦ä¸²
 */
export function escapeHtml(text: string): string {
  const div = document.createElement("div");
  div.textContent = text;
  return div.innerHTML;
}

/**
 * å–®å‰‡è¨Šæ¯ HTML
 * @param message - æ¶ˆæ¯å…§å®¹
 * @param type - æ¶ˆæ¯é¡å‹
 * @param colorClass - CSS é¡è‰²é¡å
 * @returns HTML å­—ç¬¦ä¸²
 */
export function createMessageHtml({
  message,
  colorClass,
}: {
  message: ConsolePayload | ErrorPayload;
  colorClass: Record<string, string>;
}): string {
  if ("level" in message) {
    return `
      <div class="console-message border-b border-gray-200 p-2 hover:bg-gray-50">
        <span class="text-xs font-bold ${colorClass[message.level]}">[${message.level}]</span>
        <span class="text-xs text-gray-400 ml-2">[${new Date().toLocaleTimeString()}]</span>
        <pre class="text-sm font-mono mt-1 whitespace-pre-wrap">${escapeHtml(message.message)}</pre>
      </div>
    `;
  }
  return `
    <div class="console-message border-b border-gray-200 p-2 hover:bg-gray-50">
      <span class="text-xs font-bold  ${colorClass["error"]}">[${message.name}]</span>
      <span class="text-xs text-gray-400 ml-2">[${new Date().toLocaleTimeString()}]</span>
      <pre class="text-sm font-mono mt-1 whitespace-pre-wrap">${escapeHtml(message.message)}</pre>
    </div>
  `;
}

/**
 *  è¨Šæ¯æ¸…å–® HTML
 * * @param messages - æ¶ˆæ¯æ•¸çµ„
 * @param type - æ¶ˆæ¯é¡å‹
 * @param colorClass - CSS é¡è‰²é¡å
 * @returns HTML å­—ç¬¦ä¸²
 */
export function createMessageListHtml({
  messages,
  colorClass,
}: {
  messages: (ConsolePayload | ErrorPayload)[];
  colorClass: Record<string, string>;
}): string {
  if (messages.length === 0) {
    return `<div class="text-gray-500 p-4">No messages</div>`;
  }
  return messages
    .map((msg) => createMessageHtml({ message: msg, colorClass: colorClass }))
    .join("");
}

/** å®¹å™¨åŒ…ä¸€å±¤ï¼ˆç”± renderer æä¾› classNameï¼‰ */
export function buildContainer(innerHtml: string, className: string): string {
  return `<div class="${className}">${innerHtml}</div>`;
}

/** all åˆ†é çš„å½™æ•´ HTMLï¼ˆç´”å‡½å¼ï¼‰ */
export function renderAllMessages({
  messages,
  containerClass = "bg-white border border-gray-200 rounded-lg max-h-96 overflow-y-auto",
}: {
  messages: (ConsolePayload | ErrorPayload)[];
  containerClass: string;
}): string {
  const allMessages = messages
    .slice()
    .sort((a, b) => b.timestamp - a.timestamp);

  if (allMessages.length === 0) {
    return '<div class="text-gray-500 p-4">No messages</div>';
  }

  return `<div class="${containerClass}">
  <div class="console-message border-b border-gray-200 p-2 hover:bg-gray-50">
  </
    ${createMessageListHtml({ messages: allMessages, colorClass: LEVEL_COLOR_MAP })} })}
  </div>`;
}
